import React from 'react';
import './previewBar.css';
import user_icon from '../../assets/user.png';



const PreviewBar = (props) => {
    return (
        <div className="previewBar">
            <div>
            <div className='userSection'>
                <img src={user_icon} alt='User' />
                
                <div >
                    <p className='bold'>Anandi Bahu</p>
                    <div className="user">
                        <p className='roll'>4101 </p>
                        <p className='roll'>EnTC A</p>
                    </div>                    
                </div>
                
            </div>
            </div>
            <br/>
            
                {props.comp?<div className="mainly">
                    <p className="bold">{props.comp.company}</p>
                    <p className="type">{props.comp.type}</p>
                    <p className="ctc">{props.comp.ctc}</p>
                    <p className="description">{props.comp.description}</p>
                    <p className="description">{props.comp.contact}
                    <br/>
                    <br/>
                    Email:
                    </p>
                    <p className="email">{props.comp.Email}</p>
                    <br/>
                    <p className="heading">CTC Break Points</p>
                    <p className="description semibold data"><div>Total :</div> <div>{props.comp.ctc}</div></p>
                    <p className="description  data"><div>Basic :</div> <div>{props.comp.basic}</div></p>
                    <p className="description  data"><div>Gross :</div> <div>{props.comp.gross}</div></p>
                    <p className="description  data"><div>Accomadation :</div> <div>{props.comp.accomadation}</div></p>
                    <br/>
                    <br/>
                    <br/>
                    <p className="description semibold data">Test Details:</p>
                    <p className="description  data"><div>Duration :</div> <div>{props.comp.duration}</div></p>
                    <p className="description  data"><div>Max. Marks :</div> <div>{props.comp.markx}</div></p>
                    <p className="description  data"><div>Cut-Off :</div> <div>{props.comp.cut}</div></p>
                </div>:<div className="nully">
                    Please select a company.
                    </div>}
            
        </div>
    )    

}

export default PreviewBar;

